.section {
  background: white;
  margin: 10px 0;
  box-shadow: 0 0 5px #ddd;
}

.section__header {
  display: flex;
  justify-content: space-between;
  border-bottom: 3px solid #f4f4f4;
}

@media (max-width: 767px) {
  .section__header {
    flex-direction: column;
  }
}

.section__title {
  margin: 0;
  color: #222;
  padding: 20px;
}

.section__body {
  padding: 20px;
}
